import React, { useContext, useEffect, useState } from 'react'
import { CartContext } from '../context/cartContext'
import IconButton from "@mui/material/IconButton"
import StyledBadge from "@mui/material/Badge"
import ItemCart from './itemCart'
import { Link } from 'react-router-dom'
import { formatPrice } from './formatPrice'

const Cart = () => {

    const [cartOpen, setCartOpen] = useState(false)
    const [productsLength, setProductslength] = useState(0)

    const {cartItems, emptyCart} = useContext(CartContext)

    useEffect(()=> {
        setProductslength(
            cartItems.reduce((previous, current) => previous + current.amount, 0)
        )
    }, [cartItems])

    const total = cartItems.reduce(
        (previous, current) => previous + current.amount * current.retail_price_cents, 0)


    return (
        <div className="cartContainer">
            <div className="buttonCartContainer">
                <div className='buttonCart'>
                <IconButton 
                    style={{zIndex: "1"}}
                    onClick={()=> {
                    setCartOpen(!cartOpen)
                }} aria-label="cart">
                    <StyledBadge badgeContent={productsLength} color="primary">
                        {!cartOpen ? (
                            <img src="data:image/png;base64," alt='cartImage'
                            style={{marginBottom: "10px"}}
                            />
                            
                        ) : (
                            <>
                            <img onClick={() => emptyCart()} src="data:image/png;base64," alt='emptyIcon'></img>
                            <img src="data:image/png;base64," alt="closeCart"></img>
                            </>
                        )}
                    </StyledBadge>
                </IconButton> 
                </div>
            </div>
            {cartItems && cartOpen && (
                <div className="cart">
                    <h2>Nike - Life Cart</h2>

                    {cartItems.length === 0 ? (
                        <p className="cartEmpty">The cart is empty</p>
                    ) : (
                        <div className='productsContainer'>
                            {cartItems.map((item, i) => (
                            <ItemCart key={i} item={item} />
                            ))}
                        </div>
                    )}

                    <h2 className="total">Total : {formatPrice(total)}</h2>
                    <div style={{"textAlign":"center", "paddingBottom":"10px"}}>
                        <Link to="/checkout">
                            <button  className="btn btn-danger">Go Checkout</button>
                        </Link>
                    </div>
                </div>
            )}
        </div>

    )
}

export default Cart
